<template>
	<div class="r">
			<!--返回键 搜索栏 -->
		<div class="top">
				<router-link to="/all/index"><mt-button><</mt-button></router-link>
				<mt-search
				  v-model="value"
				  cancel-text="取消"
				  placeholder="搜索">
				</mt-search>	
		<img src="../assets/images/rank.jpg" alt=""  width="100%" >
		</div>
		<!-- 排行列表 -->
		<div class='list'>
		<!-- infinite-scroll-immediate-check 当没有数据时不加载 -->
			<ul
			  v-infinite-scroll="loadMore"
			  infinite-scroll-disabled="loading"
			  infinite-scroll-immediate-check="false"   
			  infinite-scroll-distance="10">
			  <li v-for="(item,index) in list.ranksongsList" @click="play(index)">
			     {{index+1}}
			     {{item.songName}}
			     <img src="../assets/images/delete.png" alt="">
			  </li>
			</ul>
		</div>

	</div>
</template>
<script>
	import store from '../store/index'
	import {api} from '../global/api'
	export default{  
		data(){
			return{
				list:{
					ranksongsList:[]
				},
				loading:false,
				value:"",
				songSrc:""
			}
		},
		// dataType: '
		mounted:function(){
			this.getData();
		},
		methods:{
			getData:function(){
				var rankpage=this;
			    this.$http.get('../../static/jsons/rankSongs.json').then(function(response){
			    	//json数据必须是对象
			    	rankpage.list.ranksongsList=response.data.rankSongs;  
			    	console.log(rankpage.list.ranksongsList);	
			    },function(response){
			    	console.log("抱歉，新歌列表请求失败了 T_T ")
			    });
			},

			loadMore() {  //自动加载
			  this.loading = true;
			  setTimeout(() => {
			    let last = this.list.ranksongsList[this.list.ranksongsList.length - 1];
			    for (let i = 1; i <= 10; i++) {
			      this.list.ranksongsList.push(last + i);
			    }
			    this.loading = false;
			  }, 2500);
			},

			//播放
			play(index){  //index为列表中的索引
				var that=this;
				var oneHash=this.list.ranksongsList[index].hash;
				console.log("歌曲的hash：",oneHash)  //可拿到hash
				//jsonp 解决ajax跨域请求问题    this.axios.get
				let url = '/yy/index.php?r=play/getdata&hash='+oneHash

   				//http://www.kugou.com/yy/index.php?r=play/getdata&hash=DF3926E1293530EF351F52B9C2260BE7
				this.$http.get(url).then(function(response){
						// that.list.ranksongsList[index].songName.setAttribute("color","blue")
						console.log("response：",response);
						//this.list=response.data.data;   //返回的json数据对象
						console.log("赋值后的list",response.data.data)
					    store.dispatch('com_State',response.data.data) //可取到mp3文件
				    },function(response){
					    console.log("抱歉，新歌列表请求失败了 T_T ",response)
				});

			}
		}
	}
</script>
<style scoped>
    .r{ width: 100%;
    	height:100%;
    	background: url('../assets/images/iBack.jpg') no-repeat;
    	background-size:cover;
    	overflow: hidden;
    	}
	.top{position: fixed;
	}
	.mint-search {
	   float:right;
	   width: 85%;
       height: 7%;
       margin-bottom:-0.1rem;
      }
    .mint-button{
     	background:#d9d9d9;
     	font-family:"宋体"; 
     	float: left;
     	font-size:2rem;
     	width: 15%;
     	height:3.1rem;
     	border-radius: 0px;
     	
     }
     .list{
     	   margin-top:-0.3rem;
     	   padding-left: 4%;
     	   padding-right:8%;
     	   padding-top: 48%;
     	   overflow:hidden;
     	  }
     .list img{
		float:right;
		padding-top:1rem;
		padding-right:1rem;
		width:5%;
	}
     .list ul{
     	margin:0px;
     	padding:0px;
     }
     .list ul li{
     	width:100%;
     	height:2.7rem;
     	list-style:none;
     	color: #000;
     	border-top:0.5px solid #d9d9d9;
     	text-align:left;
     	line-height:2.7rem;
     	padding:0px 1% 0px 3%;
     }
      .mint-tab-item {
         position: fixed;
         bottom: 0px;
         width: 2.5rem;
         height: 2.5rem;
         padding: 4px 0;
     }
     .mint-tab-item-icon > * {
         width: 2.5rem;
         height:2.5rem;
     }
     .bloser{width: 100%;
     	    height: 3rem;
     	    background:rgba(108,108,108,0.8);
     		position: fixed;
     		bottom: 0px;
     	}
     .top img{margin-top: -0.1rem;}
     .bloser img{float: left;}
	  audio{float:right;
	 	   padding-right:1rem;
	 	   padding-top:0.5rem;
	 	   }

</style>


