<template>
	<div class="i"> 
		<!-- 头部 -->
	  	<div class="a">
	  		<div class="top">
	  			<img src="../assets/images/logo.png" alt="" height="80%" >
	  			<p>我好像在哪见过你</p>
	  		</div>
	  		<div class="nav">
	  			 <ul>
	  			 	<li>首页</li>
	  				<router-link to="/all/rank"><li>排行</li></router-link>
	  				<router-link to="/all/songList"><li>歌单</li></router-link>
	  				<router-link to="/all/other"><li>其他</li></router-link>
	  			 </ul>
	  		</div>
	  		<!-- 轮播 -->
	  		<mt-swipe :auto="4000">
	  		  <mt-swipe-item><img src="../assets/images/a.jpg"></mt-swipe-item>
	  		  <mt-swipe-item><img src="../assets/images/b.jpg"></mt-swipe-item>
	  		  <mt-swipe-item><img src="../assets/images/c.jpg"></mt-swipe-item>
	  		</mt-swipe> 
	        </div>

		<div class="content">
			<div class="recommend">
			<router-link to="/all/songList">
				<div class="local">
				    <img src="../assets/images/mysong.png" alt="" >
					<span>本地音乐</span>
				</div>
			</router-link>
				<div class="local2">
				  <router-link to="/all/collect">
					<div class="lo2">
						<img src="../assets/images/co.png" alt="" >
						<span>收藏</span>
					</div>
				</router-link>
					<router-link to="/all/rank">
						<div class="lo2">
							<img src="../assets/images/ph.png" alt="" >
							<span>排行</span>
						</div>
					</router-link>
					
				</div>
				<div class="local2">
				  <router-link to="/all/love">
					<div class="lo2">
						<img src="../assets/images/like.png" alt="" >
						<span>我喜欢</span>
					</div>
				  </router-link>
				  <router-link to="/all/about">
				  	<div class="lo2">
				  		<img src="../assets/images/about.png" alt="" >
				  		<span>关于他</span>
				  	</div>
				  </router-link>
					
				</div>
			</div>
	</div>
	</div>
</template>
<script>
	export default{
		data(){
			return{
				Rolls:[],//轮播json数据
				newsongsList:[0],  //新歌json数据
				loading:false

			}
		},
		mounted:function(){
			this.getData();
		},
		methods:{
			//请求json轮播图
			getData:function(){  
				let page1=this;
				this.$http.get('../../static/jsons/roll.json').then(function(response){
					page1.Rolls=response.data;  //将返回的json赋值给Roll数组
					console.log(page1.Roll);
				},function(response){
					console.log("抱歉,轮播图请求失败了 T_T ")
				});
			},
			loadMore() {  //自动加载
			  this.loading = true;
			  setTimeout(() => {
			    let last = this.newsongsList[this.newsongsList.length - 1];
			    for (let i = 1; i <= 10; i++) {
			      this.newsongsList.push(last + i);
			    }
			    this.loading = false;
			  }, 2500);
			}
			
		}
	}
</script>
<style scoped>
  
 .i{
 	height: 42rem;
	background: url('../assets/images/iBack2.jpg') no-repeat;
	background-size:100% 100%;
} 
 .a{
 	width: 100%;
 	padding: auto;
	position: fixed;
  } 
.top{
	width: 100%;
	background:rgba(0,0,0,0.6);
	height:3rem;
    line-height:1.5rem;
	/*text-align: center;*/
}
.top img{float: left; 
		 margin: 1.2% 0% 2% 2%;}

p{ float:right;
   height:0px;
   color:#d9d9d9;
   font-weight:bold;
   font-size:1.5rem;
   margin-right:5%;
   font-family:"华文行楷";
}
.mint-swipe{
	height:9rem;
	width: 100%;
	clear:both;
}
/* .mint-swipe img:first-child{
	height:10rem;
} */
.nav{width:100%;
	 height:3%;
	 text-align: center;
	 clear:both;
	}
.nav ul li{
	 color:#005151;
	 list-style:none;
	 float:left;
	 margin:auto;
	 margin-right: 3.1rem;
}
.content{
	clear:both;
    text-align:left;
	width:100%;
	padding-top: 15rem;
}
.user{
	 width:12%;
	 padding-left: 10%;
	 padding-right:11%;
	 float:left;
	 text-align:center;
	 color:#005151;
}
.recommend{
	clear:both;
	margin-top:5rem;
	width:100%;
	text-align: center;
}
.local{
	margin: auto;
	width: 20rem;
	height: 6rem;
	color: #fff;
	font-family: "华文彩云";
	font-size: 2.2rem;
	line-height: 6rem;
	background:rgba(0,0,0,0.6);
}
.local img{
	width:3rem;
	padding-top:1.5rem;
	padding-left: 4rem;
	float: left;
}
.local span{
	margin-left: -3rem;
}
.local2{
	width: 22rem;
	margin: auto;
}
.lo2{
		margin: 1rem;
		margin-bottom: 0rem;
		width: 9rem;
		height: 3rem;
		color: #fff;
		font-family: "华文彩云";
		font-size: 1.3rem;
		line-height: 3rem;
		background:rgba(0,0,0,0.6);
		float: left;
	}
.lo2 img{
	width:1.2rem;
	padding-top:0.9rem;
	padding-left:1.5rem;
	float: left;
}
.lo2 span{
	margin-left: -1.2rem;
}
 .mint-tab-item {
    position: fixed;
    bottom: 0px;
    width: 2.5rem;
    height: 2.5rem;
    padding: 4px 0;
}
.mint-tab-item-icon > * {
    width: 2.5rem;
    height:2.5rem;
}
 .bloser{width: 100%;
	    height: 3rem;
	    background:rgba(151,76,96,0.95);
		position: fixed;
		bottom: 0px;}
  audio{float:right;
 	   padding-right:1rem;
 	   padding-top:0.5rem;
 	   }
</style>


