<template>
	<div class="d">
		<div class="top">
			<router-link to="/all/rank"><p><</p></router-link>
			<span v-model="songInf.name">{{songInf.name}}</span>
		</div>
		<div class="middle" v-model="songInf.lyrics">
			{{songInf.lyrics}}
		</div>
		<div class="middle2">
			<img src="../assets/images/sc.png" width="9%">
			<img src="../assets/images/love.png"  width="11%">
		</div>
		<div class="bottom">
			<audio :src="songInf.songUrl" controls autoplay loop></audio>
			<div class="imgs">
				<img class="img1" src="../assets/images/last.png" width="20%">
				<img class="img2" src="../assets/images/stop.png" width="30%" @click="paly">
				<img class="img3" src="../assets/images/next.png" width="20%">
			</div>
			
		</div>
	</div>
	</template>
<script>
	import store from '../store/index'
	export default {
	  name: 'app',
	  data(){
	    return{
	    	songInf:store.state.listSong.audio
	    }
	  },
	  mounted:function(){
	  	console.log("----------",this.songSrc);
	  },
	  methods:{
	    paly(){
	    	var myaudio=this.songSrc.songUrl;
	    	if(myaudio.paused){   //paused()暂停播放
	    		myaudio.play();
	    	}else{
	    		myaudio.paused();
	    	}
	    }
	  }
	}
</script>
<style scoped>
   
	*{margin:0rem;
	  padding:0rem;
	}
	.d{
		width:100%;
		height: 42rem;
		background:url('../assets/images/dBack.jpg') no-repeat;
	}
	
	.top{
		width: 100%;
		height:4rem;
	    line-height:2rem;
		text-align: left;
	}
	p{
		font-family:"宋体";
		color:#028fe2; 
		float: left;
		font-size:3rem;
		padding-left:1rem;
		padding-top:0.8rem;
	}
	span{
		width:100%;
		line-height:3.5rem;
		color:#028fe2; 
		font-size:1.5rem;
		margin-left:2rem;
	}
	.middle{
		width:100%;
		height: 20rem;
		overflow:auto;
	}
	.middle2{
		padding:2rem 3rem 0rem 3rem;

	}
	.middle2 img:first-child{
		float: left;
	}
	.middle2 img:last-child{
		float: right;
	}
	.bottom{
		width: 100%;
		height: 30%;
		position: fixed;
		bottom: 0rem;
		background:rgba(0, 0, 0, 0.2);
		padding: auto;
	}
	audio{width:100%;
		  /* margin-bottom:2rem; */
		}
	.imgs{
		width:100%;
		height:10rem;
		display:table-cell;
	    vertical-align:middle;
	}
    .img1,.img3{margin-bottom:1rem; }
	.img2{
		margin-left: 2rem;
		margin-right:2rem;
	 }
</style>


