	<template>
		<div>

			<router-view></router-view>
			<!-- 底部播放器 -->
			<router-link to="/detail">
			  <div class="bloser">
			    <mt-tab-item>
			      <img slot="icon" src="../assets/images/bottom.jpg" @click="bb">
			    </mt-tab-item>
			     <audio :src="songSrc.songUrl" controls autoplay loop></audio>
			  </div>
			</router-link>
		</div>
	</template>
	<script>
	import store from '../store/index'
	 export default{
	 	data(){
	 	  return{
	 	     songSrc:store.state.listSong.audio
	 	  }
	 	},
	 	mounted:function(){
	 	      console.log("1111111111",this.songSrc)
	 	},
	 	methods:{
	 	   bb(){
	 	   	  console.log("2222222222",this.songSrc.songUrl)
	 	   }
	 	}
	 }
		
	</script>
	<style>
		 .bloser{width: 100%;
		         height: 3rem;
		         background:rgba(0,0,0,0.5);
		         /*background: #fafafa;*/
		         position: fixed;
		         bottom: 0rem;
		}
		 .mint-tab-item {
		         width:10%;
		         float: left;
		         padding-top:0.4rem;
		}
		 .mint-tab-item img{ 
		         width:2.3rem;
		         height:2.3rem;
		}
		audio{
		         float:right;
		         width: 80%;
		         padding-right:1rem;
		         padding-top:0.5rem; 
		}
	</style>


